.slick-slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }
  .slick-slider .slick-list, .slick-slider .slick-track, .slick-slider .slick-track div {
    width: 100%;
    height: 100%; }
  .slick-slider .image-container {
    width: 100%;
    height: 100%;
    background-color: rgba(11, 55, 63, 0.8);
    background-blend-mode: darken;
    background-size: cover;
    background-position-x: 0%;
    background-position-y: center;
    -webkit-animation: sliding 10s linear infinite .5s;
            animation: sliding 10s linear infinite .5s; }
    .slick-slider .image-container.image-a {
      background-image: url("../../../assets/img/main_picture_01.jpg"); }
    .slick-slider .image-container.image-b {
      background-image: url("../../../assets/img/main_picture_02.jpg"); }

@-webkit-keyframes sliding {
  0% {
    background-position-x: 0%; }
  100% {
    background-position-x: 100%; } }

@keyframes sliding {
  0% {
    background-position-x: 0%; }
  100% {
    background-position-x: 100%; } }



